<template>
  <div class="register" ref="father">
    <div id="Verification" v-text="Verification" @click="randomNum"></div>
    <span id="passwordjudge"></span>
    <van-nav-bar title="" left-arrow @click-left="onClickLeft" />
    <van-field
      v-model="phone"
      label=""
      placeholder=""
      required
      autocompleted="off"
      clearable
      @blur="phonejudge1()"
      ref="phone"
    />
    <van-field
      v-model="password"
      type="password"
      label=""
      placeholder=""
      required
      autocompleted="off"
      clearable
    />
    <van-field
      v-model="password2"
      type="password"
      label=""
      placeholder=""
      required
      clearable
      @blur="passwordjudge1()"
      ref="password"
    />
    <van-field
      v-model="VerificationCode"
      label=""
      placeholder=""
      autocomplete="off"
      required
      @blur="codejudge1()"
    />
    <van-button type="primary" size="large" @click="showPopup()"></van-button>
    <van-popup v-model="show">
      <p :class="{
        mess : true,
      }">{{mess}}</p>
    </van-popup>
  </div>
</template>
<script>
export default {
  methods: {
    onClickLeft() {
      window.location.hash = "#/";
      console.log(window.location.hash);
    },
    randomNum() {
      var html =
        "0987654321zxcvbnmlkjhgfdsaqwertyuiopZXCVBNMLKJHGFDSAQWERTYUIOP";
      var code = ""; //
      for (var i = 0; i < 4; i++) {
        var ran = parseInt(Math.random() * html.length);
        code += html[ran];
      }
      this.Verification = code;
    },
    //
    phonejudge1() {
      let reg = /^1[3-9]\d{9}$/;
      this.phonejudge = reg.test(this.phone);
      let a = this.$refs.phone.$el;
      let b = a.querySelector("input");
      if (!this.phonejudge) {
        //
        b.style.color = "red";
      } else {
        b.style.color = "#000";
      }
    },
    passwordjudge1() {
      if (this.password != this.password2) {
        this.$refs.father.querySelector("#passwordjudge").style.display =
          "block";
      } else {
        this.$refs.father.querySelector("#passwordjudge").style.display =
          "none";
        this.passwordjudge = true;
      }
    },
    codejudge1() {
      if (this.VerificationCode == this.Verification) {
        this.codejudge = true;
      }
    },
    //
    async showPopup() {
      this.show = true;
      if (this.phonejudge && this.passwordjudge && this.codejudge) {
        this.mess = "";
        let str = await this.$axios({
          method: "post",
          url: "http://localhost:10130/register",
          data: {
            phone: this.phone,
            password: this.password
          }
        });
        console.log(str);
      } else {
        this.$refs.father.querySelector(".mess").style.color = "red";
        this.mess = "";
      }
    }
  },
  data() {
    return {
      password: "",
      Verification: "",
      show: false,
      //
      phone: "",
      password2: "",
      VerificationCode: "",
      phonejudge: "",
      passwordjudge: "",
      codejudge: "",
      //
      mess: ""
    };
  },
  created() {
    this.randomNum();
    // console.log(this);
  },
  mounted() {}
};
</script>
<style scoped>
.register {
  background: #efeff4;
  height: 100%;
}
.register .van-nav-bar {
  background: #3993cf;
}
.register .van-nav-bar__title {
  color: #fff;
}
.register .van-nav-bar__arrow {
  font-size: 20px;
  color: #fff;
}
.register .van-button--large {
  width: 90%;
  margin-left: 0.5rem;
  margin-top: 0.6rem;
  background-color: #3993cf;
  border-radius: 0.1rem;
  height: 1.4rem;
  line-height: 1.4rem;
}

.register #Verification {
  width: 3rem;
  height: 1.3rem;
  position: absolute;
  background: #3993cf;
  top: 5.6rem;
  right: 0.2rem;
  z-index: 999;
  line-height: 1.3rem;
  text-align: center;
  font-size: 18px;
  color: white;
}
.register #passwordjudge {
  color: red;
  position: absolute;
  top: 4.5rem;
  right: 0.1rem;
  z-index: 999;
  display: none;
}
.register .mess {
  line-height: 2rem;
  color: #58bc58;
  font-size: 0.5rem;
  display: block;
  width: 4rem;
  height: 2rem;
  text-align: center;
}
.register .mess.err {
  color: red;
}
</style>


